.App {
  text-align: center;
  
}

.Headword{
  background-color: red;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #171718;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1{
  color: white;
  font-weight: 20;
}

.Headword{
  background-color: black;
  margin-top: 5%;
  width: 100%;
  color: white;
  font-size: 50px;
  text-align: center;
  font-weight: 100;
}